<template>
  <div class="tree-view-atom">
    <ul class="tree-view-atom__ul">
      <li v-for="item of folders" :key="item.id" class="tree-view-atom__li">
        <div @click="expandCollapse(item)" :class="{
          'tree-view-atom__ul__li__divlabel-selected': (selectedTreeTrace != null ? selectedTreeTrace.includes(item.id) : false),
          'tree-view-atom__ul__li__divlabel': (selectedTreeTrace != null ? !selectedTreeTrace.includes(item.id) : true)
          }">
          <i v-if="item.collapsed" class="el-icon-arrow-right"></i>
          <i v-if="!item.collapsed" class="el-icon-arrow-down" ></i>
          <label>{{item.text}}</label>
        </div>
        <div class="tree-view-atom__ul__li__divchildren" v-if="!item.collapsed">
          <TreeViewAtom v-if="item.children" :items="item.children" @itemSelected="itemSelectedChild(item,$event)" :groupIcon="groupIcon" :selectedTreeTrace="selectedTreeTrace"/>
        </div>
      </li>
      <li v-for="item of files" :key="item.id" class="tree-view-atom__li">
        <div :class="{
          'tree-view-atom__ul__li__divlabel-selected': (selectedTreeTrace != null ? selectedTreeTrace.includes(item.id) : false),
          'tree-view-atom__ul__li__divlabel': (selectedTreeTrace != null ? !selectedTreeTrace.includes(item.id) : true)
          }">
          <img v-if="item.icon" :src="getAssetImage(item)" :style="{'opacity':0.6}"/>
          <label @click="itemSelectedCurrent(item)">{{item.text}}</label>
        </div>

      </li>
    </ul>
  </div>
</template>

<script>
import TreeViewAtomHook from './TreeViewAtom.hook'
const hook = new TreeViewAtomHook();

const sortCompare = (a, b) => {
  const editableA = (a.text || "").toUpperCase();
  const editableB = (b.text || "").toUpperCase();

  let comparison = 0;
  
  if (editableA > editableB) {
    comparison = 1;
  } else if (editableA < editableB) {
    comparison = -1;
  }
  return comparison;
}

export default {
  name: 'TreeViewAtom',
  beforeCreate() { hook.hook(this)},
  methods: {
    expandCollapse(item){
      const cf = this.openedDocument.collapsedFolders;
      if (cf[item.currentFolder] == undefined || cf[item.currentFolder] == null){
        this.$set(cf,item.currentFolder,true);
      }else {
        this.openedDocument.collapsedFolders[item.currentFolder] = !cf[item.currentFolder];
      }
      
    },
    itemSelectedCurrent(item){
      if (item.children){
        item.collapsed = !item.collapsed;
      }else {
        const trace = [item.id];
        this.$emit("itemSelected", {
            item,
            trace
          }
        );
      }
    },
    itemSelectedChild(item,$event){
      $event.trace.push(item.id);
      this.$emit("itemSelected", $event);
    },
    getAssetImage(item) {

        let image="";
        
        if (item.children){
          image = this.groupIcon;
        }else {
          image = item.icon;
        }

        if (image){
          const images = require.context('../../../../Frontend/public/assets/images/');
          return images(`./${image}`);
        }

    }
  },
  components: {
    TreeViewAtom: this
  },
  props: { 
    items: Array,
    groupIcon: String,
    selectedItemId:String,
    selectedTreeTrace: Array
  },
  data() {
      return {

      }
  },
  computed: {
    folders(){
      return this.items?.filter(item => item.children).sort(sortCompare) || [];
    },
    files(){
      return this.items?.filter(item => !item.children).sort(sortCompare) || [];
    },
    ...hook.getters("openedDocument")
  }
}

</script>

<style scoped lang="scss">

.tree-view-atom {
  border-left: 1px dotted rgb(100, 100, 100);

  &__ul {
    list-style:none;
    color: rgb(202, 202, 202);
    font-size:14px;
    padding-left:25px;
    &__li {
      

      @mixin divLabel(){
        
        transition: 0.25s;
        display: flex;
        align-items: center;
        img{
          max-height:20px;
          
          margin-bottom:4px;
          margin-right:10px;
          margin-left:-8px;
        }

        i{
          font-size:16px;
          font-weight: bold;
          margin-top:4px;
          margin-bottom:4px;
          margin-left:-8px;
          margin-right:10px;
          cursor: pointer;
        }

        label {
          cursor: pointer;
        }
      }

      &__divlabel{
        @include divLabel();
      }

      &__divlabel-selected {
        @include divLabel();
        color: rgb(127, 219, 127);
      }

    }
  }

}

</style>
