<template>
  <div class="detailed-view-molecule lc-custom-scroll">
    <TreeViewAtom :items="allEditables" @itemSelected="itemSelected" :selectedTreeTrace="selectedTreeTrace"/>
  </div>
</template>

<script>
import DetailedViewMolecule from './DetailedViewMolecule.hook'
import {EDITABLE_TYPES} from '../../../../../Common/VariousTypes'; 
import TreeViewAtom from '../../../../UiCommon/Atoms/TreeViewAtom/TreeViewAtom'
import {switchEditable} from '../../../../../Frontend/Helpers/editableHelpers';


const hook = new DetailedViewMolecule();
export default {
  name: 'DetailedViewMolecule',
  beforeCreate() { hook.hook(this)},
  components:{
    TreeViewAtom
  },
  data() {
      return { 
        selectedTreeTrace: []
      }
  },
  methods: {
    itemSelected({item,trace}){
      const editable = this.openedDocument.editables.find(editable => editable.id == item.id);
      if (editable){
        this.selectedTreeTrace = trace;
        switchEditable(hook,this,editable);
      }
    }
  },
  computed: {
    collapsedFolders(){
      return this.openedDocument?.collapsedFolders || {};
    },
    allEditables :function () { 
      
      const rootObject = {
        children:[]
      };
      let folderId =0;
      const availableFolders = [];

      this.openedDocument.editables.forEach(editable=>{

        let text;
        let icon;

        let editableProps = editable.properties["0"];
        const editableId = editable.id;

        switch (editable.type){
          case EDITABLE_TYPES.MODULE:
            icon = "puzzle-piece-solid.svg";
            break;
          case EDITABLE_TYPES.API:
            text = `${editableProps.method} ${editableProps.path}`;
            icon = "api.svg";
            break;
          case EDITABLE_TYPES.DOCUMENT_LEVEL:
            text = editableProps.title;
            icon = "globe-solid.svg";
            break;
          case EDITABLE_TYPES.ADMIN:
            icon = "adminscript.svg";
            break;
          case EDITABLE_TYPES.CRON:
            icon = "clock-regular.svg";
            break;
          case EDITABLE_TYPES.SCHEMA:
            icon = "schema.svg";
            break;
          case EDITABLE_TYPES.SCRIPT:
            icon = "python-brands.svg";
            break;
          case EDITABLE_TYPES.SQL:
            icon = "file-sql.svg";
            break;
          case EDITABLE_TYPES.HTML:
            icon = "file-html.svg";
            break;
          case EDITABLE_TYPES.JSON:
            icon = "file-js.svg";
            break;
          case EDITABLE_TYPES.CSV:
            icon = "file-csv.svg";
            break;
          case EDITABLE_TYPES.TXT:
            icon = "file-csv.svg";
            break;
        }

        let parentObj = rootObject;

        const group = editableProps.group;
        
        if (group){
          
          const groupParts = group.split("/");
          let currentFolder = null;

          for(const part of groupParts){
            if (currentFolder == null){
              currentFolder = part;
            }else {
              currentFolder = `${currentFolder}/${part}`;
            }
            const isCollapsed = this.collapsedFolders[currentFolder] || false;

            let child = parentObj.children.find(obj => obj.text == part);
            if (!child){
              availableFolders.push(currentFolder);
              folderId++;
              child = {text:part,children:[], id:`${folderId}`, collapsed:isCollapsed, currentFolder};
              parentObj.children.push(child);
            }
            parentObj = child;
          }
        }

        parentObj.children.push({text : text || editableProps.name, icon, id:editableId});

      });

      Object.keys(this.collapsedFolders).forEach(folder => {
        if (!availableFolders.includes(folder)){
          delete this.collapsedFolders[folder];
        }
      });

      return rootObject.children;
    },
    ...hook.getters("openedDocument","selectedItems")
  }
}

</script>

<style scoped lang="scss">

.detailed-view-molecule {
  height:100%;
  overflow: auto;
}

</style>
